import React from 'react';
import { NavLink } from 'react-router-dom';

const navigation = () => {
    return (
        <div className="navigation">
            <NavLink exact to="/" activeClassName="nav-active">
                Description
            </NavLink>
            <NavLink exact to="studies" activeClassName="nav-active">
                Parcours Scolaire
            </NavLink>
            <NavLink exact to="pro" activeClassName="nav-active">
                Pacours Professionnel
            </NavLink>
            <NavLink exact to="abilities" activeClassName="nav-active">
                Compétences
            </NavLink>
        </div>
    );
};

export default navigation;