import React, {useState, useEffect} from 'react';
import CardStudies from './CardStudies';

const StudiesContainer = () => {
    const [data, setData] = useState([]);
    const studies = [
        ["2018","B&R,Eggelsberg (Autriche)","Workshop : Innovative Motion - SuperTrak et robotique","https://www.br-automation.com/fileadmin/1436233520682-de-html-1.0.jpg"],
        ["2017-2018","Lyon I - La Doua","Licence Professionnelle en Automatisme S2IP (Systèmes Intelligents Interconnectés & Pilotés","https://static.latribune.fr/full_width/398887/campus-la-doua-lyon-i-claude-bernard.jpg"],
        ["2014-2016","Lycée CARRIAT - Bourg-en-Bresse","BTS Electrotechnique","https://cdn-s-www.leprogres.fr/images/49142F1F-0542-4B87-845C-43A54113FC62/MF_contenu/la-salle-polyvalente-du-lycee-carriat-ouverte-aux-burgiens-1536779235.jpg"],
        ["2011-2014","Lycée CARRIAT - Bourg-en-Bresse","Baccalauréat S (Science de l'Ingénieur-Spécialité Mathématiques)","https://cdn-s-www.leprogres.fr/images/49142F1F-0542-4B87-845C-43A54113FC62/MF_contenu/la-salle-polyvalente-du-lycee-carriat-ouverte-aux-burgiens-1536779235.jpg"]   
]
    useEffect(() => {
        setData(studies)
    },[]);
    
    return (
        <div className="studies-container">
            <ul className="studies-list">
                {data.map((studie) => (
                    <CardStudies studie={studie} key={studie[2]} />
                ))}
            </ul>
        </div>
    );
};

export default StudiesContainer;