import React, { useState, useEffect} from 'react';

const CardStudies = (props) => {
    return (
        <div className="card-studies">
            <div className="year">{props.studie[0]}</div>
            <div className="location">{props.studie[1]}</div>
            <div className="studie-information">{props.studie[2]}</div>
            <img className="image" src={props.studie[3]} alt="image" />
        </div>
    );
};

export default CardStudies;