import React, { useState, useEffect} from 'react';

const CardPro = (props) => {
    return (
        <div className="card-pro">
            <div className="year">{props.pro[0]}</div>
            <div className="time">{props.pro[1]}</div>
            <div className="company">{props.pro[2]}</div>
            <div className="work">{props.pro[3]}</div>
            <div className="description">{props.pro[4]}</div>
            <img className="image" src={props.pro[5]} alt="image" />
        </div>
    );
};

export default CardPro;